.aboutme {
    border-top: thick;
    border-color: $color-secondary;
    width:99vw;
    height:fit-content;
    padding-bottom: 2rem;
    background: url('../../assets/Header-Background6.jpg') no-repeat center center; 
        -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;

    &__header {
        font-size: large;
        color:$color-secondary;
        font-size:8rem;
        display:flex;
        flex-direction: center;
        padding-left:38%;
        text-align:center;
    }

    &__about {
        color:$color-white;
        font-size:4rem;
        text-align:center;
        line-height: normal;
        margin-bottom:3rem;
    }

    &__content {
        display:grid;
        grid-template-columns: 33vw 33vw 33vw;
        align-content: center;
        margin-left: 4vw;

        &__education {
            width:25vw;

            &__title {
                color:$color-white;
                font-size:4rem;
            }
            &__content {
                border:thick;
                border-color: white;
                border-style: solid;
                border-radius: 35px;
                height:40vw;
                width:100%;
                cursor: pointer;
                :hover {
                    border:thick;
                    border-color: $color-secondary;
                    border-style: solid;
                    border-radius: 35px;
                    transition: all 0.5s;
                }

                img {
                    width:100%;
                    height:100%;
                }
            }
        }

        &__central {
            width:25vw;
            margin-right:6.25vw;
            &__experiences {
                &__title {
                    color:$color-white;
                    font-size:4rem;
                }

                &__content {
                    border:thick;
                    border-color: white;
                    border-style: solid;
                    border-radius: 35px;
                    height:15vw;
                    width:100%;

                    :hover {
                        border:thick;
                        border-color: $color-secondary;
                        border-style: solid;
                        border-radius: 35px;
                        transition: all 0.5s;
                    }

                    img {
                        width:100%;
                        height:100%;
                    }
                }
            }
            &__projects {
                margin-top:5rem;
                &__title {
                    color:$color-white;
                    font-size:4rem;
                }

                &__content {
                    border:thick;
                    border-color: white;
                    border-style: solid;
                    border-radius: 35px;
                    height:15vw;
                    width:100%;

                    :hover {
                        border:thick;
                        border-color: $color-secondary;
                        border-style: solid;
                        border-radius: 35px;
                        transition: all 0.5s;
                    }

                    img {
                        width:100%;
                        height:100%;
                    }
                }
            }
        }

        &__resume {
            width:25vw;
            
            &__title {
                color:$color-white;
                font-size:4rem;
            }

            &__content {
                border:thick;
                border-color: white;
                border-style: solid;
                border-radius: 35px;
                height:40vw;
                width:100%;

                :hover {
                    border:thick;
                    border-color: $color-secondary;
                    border-style: solid;
                    border-radius: 35px;
                    transition: all 0.5s;
                }

                img {
                    width:100%;
                    height:100%;
                }
            }

        }
    }
}